import userobject from "@/components/script/userobject";

export default {
  name: "buy_theav",
  mixins: [userobject],
  props: {
    topic_data: {},
  },
  data() {
    return {
      buy_loading: false,
      buy_disable: true,
      buy_confirm: false,
      buy_gold_or_vip_confirm: false,
      buy_items: {}
    }
  },
  destroyed() {
    this.listener_loginout.off();
  },
  created() {
    console.log('vip_degree:', this.vip_degree);
    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });

    if (this.$store.state.hasLogin && !this.$store.state.user.userinfo.is_vip) {
      this.load_buy_item();
    }
  },
  computed: {
    vip_degree: function () {
      return this.this_vip_degree(this.$store.state.user.userinfo);
    },
    not_engout_gold: function () {
      try {
        return this.topic_data.buy_theav.price > this.$store.state.user.userinfo.gold;
      } catch (e) {

      }
      return false;
    },
    torrent_url: function () {
      var mktorrent = this.topic_data.mktorrent;
      if (mktorrent && mktorrent.torrent_id) {
        return "/download/torrent/" + this.topic_data.id;
      }
      if (this.topic_data.video_info) {
        var mktorrents = this.topic_data.video_info.mktorrents;
        if (mktorrents && mktorrents.length > 0) {
          return "/download/torrent/" + this.topic_data.id;
        }
      }
      return undefined
    }
  },
  methods: {
    download_torrent_event: function (event) {
      if (!this.$store.state.hasLogin) {
        event.preventDefault();
        return this.$app._userbar.show_login_dialog('下载功能只对注册用户开放，请先注册或者登录！');
      }
    },
    load_buy_item: function () {
      var params = {
        'id': this.topic_data.id,
        'item': 'buy_theav',
        'type': 'topic'
      };
      this.$axios.get('/app/topic/buy/item', {
        params: params
      }).then((response) => {
        console.log('topci/buy/item:', response);
        var buy_item = response.data.buy_item;
        if (buy_item) {
          this.buy_items.buy_theav = buy_item;
          this.$forceUpdate();
        }
      });
    },
    on_buy_topic: function () {
      var post_data = {
        'vid': this.topic_data.id,
        'item': 'buy_theav',
        'type': 'topic'
      };

      this.$axios.post('/app/topic/buy', post_data).then((response) => {
        var response_data = response.data;
        console.log("on_buy_topic response_data:", response_data);
        var buy_content = response_data.buy_content;
        if (buy_content) {
          this.buy_content = buy_content;
        }
        var buy_theav = response_data.buy_theav;
        if (buy_theav) {
          this.buy_items.buy_theav = buy_theav;
        }
        this.$message(this.$l('thread.buy_success'));
        console.log('this.buy_items:', this.buy_items);
        this.$forceUpdate();
      });
    },
    buy_event: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      if (this.not_engout_gold) {
        //return this.buy_gold_or_vip_confirm = true;
      }
      this.buy_confirm = true;

    },
    on_click_file: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      if (this.not_engout_gold) {
        //return this.buy_gold_or_vip_confirm = true;
      }
      this.buy_confirm = true;
    }
  }
}
