import Vue from "vue";

export default {
  name: "view_content",
  props: {
    topic_data: {},
  },
  data() {
    return {
      show_what: 'empty_view'
    }
  },
  created() {
    console.log('create topic data');
    this.$nextTick(() => {
      console.log('[content]this.topic_data:', this.topic_data);
      var component_data = {
        topic_data: this.topic_data,
      }

      var template_string = '';
      if (this.topic_data.content.indexOf('data-src') >= 0) {
        template_string = "<div class='ql-editor' v-lazy-container=\"{ selector: 'img' }\">" + this.topic_data.content + "</div>";
      } else {
        template_string = "<div class='ql-editor' >" + this.topic_data.content + "</div>";
      }

      console.log('template_string:', template_string);

      Vue.component("view_content_component", {
        template: template_string,
        data() {
          return Object.assign(component_data, {
            photo_min_height: '260px'
          })
        },
        computed: {
          screen_shots_list: function () {
            var full_list = [];
            var online_video = this.topic_data.video_info.online_videos[0];
            var list = online_video.screenshots.list.slice(0, 6);
            var hash_id = online_video.hash_id;
            for (var i = 0; i < list.length; ++i) {
              var full_url = this.geenrate_screenshot_url(online_video.space_name, hash_id, list[i]);
              full_list.push(full_url);
            }
            return full_list
          },
          cover_image_url: function () {
            var online_video = this.topic_data.video_info.online_videos[0];
            var hash_id = online_video.hash_id;
            return this.generate_cover_url(online_video.space_name, hash_id);
          }
        },
        methods: {
          on_image_error: function () {

          }
        }
      });
      this.show_what = 'view_content_component';

    });
  },
  methods: {},
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    }
  },
}
