export default {
  name: "edit",
  props: {
    topic_data: {},
  },
  data() {
    return {
      buy_contents: undefined,
      pass_verify_dialog: false,
      not_verify_dialog: false,
      not_verify: {
        reason: "",
        action_uid: this.$store.state.user.userinfo.uid
      },
      pass_verify: {
        action_uid: this.$store.state.user.userinfo.uid,
        reward_gold: 0
      },
      topic_verify: undefined
    }
  },
  created() {
    this.buy_contents = this.topic_data.buy_contents;
    this.load_topic_verify();
  },
  methods: {
    on_not_verify: function () {
      this.not_verify_dialog = true;
    },
    on_pass_verify: function () {
      this.pass_verify_dialog = true;
    },
    on_dialog_pass_verify: function () {
      var post_data = Object.assign({}, this.pass_verify);
      console.log('[on_dialog_pass_verify]:', post_data);
      post_data['topic_id'] = this.topic_data.id;
      post_data['action'] = 'pass_verify';
      this.$axios.post('/app/topic/verify', post_data).then((response) => {
        var response_data = response.data;
        console.log("on_buy_topic response_data:", response_data);
        this.set_topic_verify(post_data);
      });
    },
    on_dialog_not_verify: function () {
      var post_data = Object.assign({}, this.not_verify);
      console.log('[on_dialog_pass_verify]:', post_data);
      post_data['topic_id'] = this.topic_data.id;
      post_data['action'] = 'not_verify';
      this.$axios.post('/app/topic/verify', post_data).then((response) => {
        var response_data = response.data;
        console.log("on_buy_topic response_data:", response_data);
        this.set_topic_verify(post_data);
      });
    },
    load_topic_verify: function () {
      this.$axios.get('/app/topic/verify', {
        'params': {
          'topic_id': this.topic_data.id
        }
      }).then((response) => {
        var topic_verify = response.data;
        console.log("load_topic_verify topic_verify:", topic_verify);
        this.set_topic_verify(topic_verify);
      });
    },
    set_topic_verify: function (topic_verify) {
      if (topic_verify) {
        this.topic_verify = topic_verify;
        var action = topic_verify.action;
        var t_a = this[action];
        t_a = topic_verify;
      }
    }
  },
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    }
  },
}
