import '../../../assets/css/editor.css';
import '../../../assets/css/quill/quill.snow.css'
import forumobjct from "../../script/forumobjct"
import view_tab from './item/tab'
import view_buy from './item/buy'
import view_content from './item/content'
import view_edit from './item/edit'
import view_video from '@/components/video/watch'
import view_watch_limit from '@/components/video/watch_limit'
import comment_reply from '../../comment/reply'
import comment_list from '../../comment/list'


export default {
  name: "threadview",
  mixins: [forumobjct],
  components: {
    view_tab,
    view_buy,
    view_video,
    view_content,
    view_edit,
    comment_reply,
    comment_list,
    view_watch_limit
  },
  data() {
    return {
      topic_data: undefined,
      forum_conf: undefined,
      edit: false,
      load_error: false
    }
  },
  created() {
    var cid = this.$route.params.id;
    var urlvars = this.getUrlVars();
    if (urlvars.edit) {
      console.log('处于编辑的帖子')
    }
    this.get_topic({
      id: cid,
      from_cache: this.from_cache(),
      edit: urlvars.edit,
      cb: this.on_load_topic_data,
      error_cb: this.on_load_error
    });
    window._t = this;

    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      console.log('event:loginout', data);
      this.$forceUpdate();
    });

    this.listener_login = this.$hub.on('event:login', (data) => {
      console.log('event:login', data);
      this.$forceUpdate();
    });


  },
  methods: {
    on_load_error: function (response) {
      console.log('on_load_error:',response);
      this.load_error = true;
    },
    on_load_topic_data: function (topic_data, edit) {
      console.log("[on_load_topic_data]topic_data:", topic_data, ' edit:', edit);
      this.topic_data = topic_data;
      this.set_title(topic_data.title);
      this.forum_conf = this.get_forum(topic_data.fid);
      this.save_topic_data(topic_data);

      if (this.topic_data.video_info && !this.show_view_watch_limit) {
        var online_video = this.topic_data.video_info.online_videos[0];
        var hash_id = online_video.hash_id;
        this.$store.state.user_visit.push(hash_id);
      }

      if (edit) {
        this.edit = edit;
      }
    }
  },
  destroyed() {
    this.listener_login.off();
    this.listener_loginout.off();

  },
  computed: {
    is_exist_video: function () {
      return this.topic_data && this.topic_data.video_info;
    },
    show_view_watch_limit: function () {
      var look_list = this.$store.state.user_visit.get_look_list();
      console.log('[show_view_watch_limit]:', look_list);
      if (!this.$store.state.hasLogin && look_list.length > 2) {
        return true;
      }
      if (look_list && look_list.length >= 5 && !this.$store.state.user.userinfo.is_vip) {
        return true;
      }
      return false;
    },
    show_fanhao: function () {
      try {
        if (this.topic_data.video_info && this.topic_data.video_info.av_data_type == 2) {
          return this.topic_data.video_info.fanhao;
        }
      } catch (e) {
        console.error(e);
      }
      return undefined
    },
    settoessence: function () {
      if (this.topic_data.manager_set) {
        return this.topic_data.manager_set.settoessence;
      }
      return undefined;
    }
  }
}
