import forumobjct from "../../../script/forumobjct";
import userobject from "../../../script/userobject";
import thread_manager from './manager';

export default {
  name: "tab",
  mixins: [forumobjct, userobject],
  components:{
    thread_manager
  },
  data() {
    return {
      defalut_tabinfo: {
        is_like: false,
        is_star: false,
        like_count: 0
      }
      ,
      like_active: undefined,
      star_active: undefined,
      tabinfo: Object.assign({}, this.defalut_tabinfo)
    }
  },
  props: {
    topic_data: {},
  },
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    }
  },
  created() {
    window._tab = this;
    if (this.$store.state.hasLogin) {
      this.get_tabinfo({id: this.topic_data.id, cb: this.on_load_tabinfo});
    }

    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.tabinfo = Object.assign({}, this.defalut_tabinfo);
      console.log('event tab loginout', this.tabinfo, ' $store.state.hasLogin:', this.$store.state.hasLogin);
      this.$nextTick(
        () => {
          console.log('event tab loginout', this.tabinfo, ' $store.state.hasLogin:', this.$store.state.hasLogin);
          this.$forceUpdate();
        }
      );
    });
    this.listener_login = this.$hub.on('event:login', (data) => {
      if (this.$store.state.hasLogin) {
        this.get_tabinfo({id: this.topic_data.id, cb: this.on_load_tabinfo});
      }
    });
  },
  methods: {
    on_load_tabinfo: function (tabinfo) {
      this.tabinfo = tabinfo;
      this.$forceUpdate();
    },
    on_tab_like: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      var post_data = {
        'id': this.topic_data.id,
        'action': !this.tabinfo.is_like
      };
      this.$axios.post('/app/topic/like', post_data).then((response) => {
        console.log("topic like:", response);
        this.tabinfo.is_like = !this.tabinfo.is_like;
        if (this.tabinfo.is_like) {
          this.tabinfo.like_count += 1;
        } else {
          this.tabinfo.like_count -= 1;
        }
      }).catch((error) => {

      });
    },
    on_tab_star: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      var post_data = {
        'id': this.topic_data.id,
        'action': !this.tabinfo.is_star
      };
      this.$axios.post('/app/topic/star', post_data).then((response) => {
        console.log("topic star:", response);
        this.tabinfo.is_star = !this.tabinfo.is_star;
      }).catch((error) => {

      });
    }
  },
  updated() {
    this.$nextTick(function () {
      console.log('tab call tab updated')
    })
  },
  destroyed() {
    this.listener_login.off();
    this.listener_loginout.off();

  }
}
